.OctoberTuners {
    background: url("../../../public/images/bg/photos.avif") no-repeat center center;
    background-size: cover;
}

.photos-overlay {
    background: var(--white-overlay);
}

.images-container {
    margin-top: 32px;
    display: grid;
    grid-template-columns: 1fr;
    gap: 32px;
}

.image-item {
    min-height: 300px;
    border-radius: 8px;
    overflow: hidden;
}

.image-item img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

@media (width > 625px) {
    .images-container {
        grid-template-columns: repeat(auto-fit, minmax(400px, 1fr));
    }
}