.Hero {
    background: url("../../../public/images/bg/hero.avif") no-repeat center center;
    background-size: cover;
}

.Hero.photos {
    background: url("../../../public/images/DSC_0122.jpg") no-repeat center center;
    background-size: cover;
}

.hero-overlay {
    min-height: 100vh;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    background: var(--black-overlay);
    color: white;
}

.hero-content {
    padding-inline: 16px;
    text-align: center;
    width: min(900px, 100%);
}

h1 span {
    font: inherit;
    color: var(--accent-base);
}

.cta-button {
    width: 96px;
    height: 96px;

    border-radius: 50%;
    border: 2px solid white;

    text-align: center;
    text-transform: uppercase;

    animation: rotate 5000ms infinite ease-in;
}

.cta-button:hover {
    background: var(--accent-base);
    color: var(--black-base);
    font-weight: 900;
    font-style: italic;
}

@keyframes rotate {
    0% {
        rotate: 0deg;
    }

    100% {
        rotate: 360deg;
    }
}