:root {
  --navbar-height: 10dvh;

  --black-base: hsl(0 0% 10%);
  --white-base: hsl(0 0% 95%);
  --accent-base: hsl(70 100% 50%);
  
  --black-overlay: hsla(0, 0%, 10%, 0.85);
  --white-overlay: hsla(0, 0%, 90%, 0.9);
  --accent-overlay: hsla(70, 100%, 50%, 0.85);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

:is(a, button) {
  all: unset;
  cursor: pointer;
}

.container {
  padding: 48px 10%;
}