.Alex {
    background: url("../../../public/images/bg/photos.avif") no-repeat center center;
    background-size: cover;
}

.Alex .photos-overlay {
    background: var(--white-overlay);
}

@media (width > 625px) {
    .images-container {
        grid-template-columns: repeat(2, 1fr);
        grid-template-rows: repeat(2, 1fr);
    }

    .large {
        grid-row: span 2;
    }
}