.navbar-links {
    width: 100%;
    min-height: var(--navbar-height);

    position: absolute;
    top: 0;

    padding-inline: 10%;
    padding-block: 24px;

    color: white;

    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: flex-end;
    gap: 16px;
}

.navbar-links a:first-child {
    margin-right: auto;
}

.navbar-links a:not(.navbar-logo)::after {
    content: "";
    display: block;
    background: white;
    width: 0;
    height: 1.5px;
    margin-top: 2px;
    transition: all 500ms;
}

.navbar-links a:not(.navbar-logo):hover::after {
    width: 100%;
}
