.footer {
    padding: 32px 10%;
    
    background: var(--black-base);
    color: var(--white-base);

    display: flex;
    flex-direction: column;
    align-items: center;

    gap: 32px;
}

.contact-info {
    display: flex;
    gap: 32px;
}

.contact-info .flex-item i {
    color: var(--white-base);
    font-size: 24px;
}

.contact-info .flex-item i:hover {
    color: var(--accent-base);
}

.copyrights {
    font-size: 15px;
    text-transform: uppercase;
    letter-spacing: 2px;
}

.copyrights a {
    color: var(--accent-base);
}

.copyrights a::after {
    content: "";
    display: block;
    width: 0;
    height: 1.5px;
    background: var(--accent-base);
    margin-top: 2px;

    transition: all 500ms;
}

.copyrights a:hover::after {
    width: 100%;
}