.Videos {
    background: url("../../../public/images/bg/videos.avif") no-repeat center center;
    background-size: cover;
}

.videos-overlay {
    background: var(--white-overlay);
}

.Videos h2 {
    width: 100%;
}

.videos-container {
    margin-block: 32px;
    display: grid;
    grid-template-columns: 1fr;
    gap: 64px;
}

.video-item {
    border-radius: 8px;
    overflow: hidden;
    aspect-ratio: 16 / 9;
}

.video-item.large {
    aspect-ratio: 9 / 16;
}

.video-item :is(iframe, video) {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

@media (width > 625px) {
    .videos-container {
        grid-template-columns: repeat(2, 1fr);
    }
}