@font-face {
    font-family: "Integral";
    src: url("../public/fonts/Integral-CF/Demo_Fonts/Fontspring-DEMO-integralcf-regular.otf");
}

@font-face {
    font-family: "Satoshi";
    src: url("../public/fonts/Satoshi/Fonts/WEB/fonts/Satoshi-Regular.woff");
}

:is(h1, h2, h3, h4, h5, h6) {
    font-family: "Integral";
    letter-spacing: 2px;
}

:not(:is(h1, h2, h3, h4, h5, h6)) {
    font-family: "Satoshi";
}

h1 {
    font-size: clamp(2.5rem, 1rem + 2.5vw, 4rem);
}

h2 {
    font-size: clamp(2rem, 0.66rem + 2vw, 3.25rem);
    width: max-content;
}

