.About {
    background: url("../../../public/images/bg/about.avif") no-repeat center center;
    background-size: cover;
}

.about-overlay {
    /* min-height: 100vh; */
    background: var(--black-overlay);
    color: var(--white-base);
}

.About h1 {
    color: var(--accent-base);
}

.About p::selection {
    background: var(--accent-base);
    color: var(--black-base);
}

article {
    width: min(580px, 100%);
}

